import React, {Component} from 'react';
import LS from "./lib/LS";

import Messenger from './core/Messenger';
export default class Logout extends Component{
    constructor(props){
        super(props);
        this.state = {
            user:{
                name:"Guest"
            }
        };

    }
    render(){
        return (
            <div className="alert alert-warning text-center">
                Đang đăng xuất...
            </div>
        );
    }           
    componentDidMount(){
        this.ioEventListener();
        this.emit('logout', {
            data:{
                access_token:Messenger.access_token,
            },
            success:'logout-success',
            fail:'logout-fail',
        });
        console.log('logout rendered');
    }

    emit = (event, data) => {
        Messenger.socket().emit(event, data);
    }

    
    ioEventListener = () => {
        const socket = Messenger.socket();
        // dang nhap thanh cong
        socket.on('logout-success', (user) => {
            LS.remove('access_token');
            Messenger.user = {};
            Messenger.access_token = null;
            console.log(Messenger);
            this.props.history.push('/login');
        });

        socket.on('logout-fail', (data) => {
            LS.remove('access_token');
            Messenger.user = {};
            Messenger.access_token = null;
            console.log(Messenger);
            this.props.history.push('/login');
        });

        
    }
}


